<template>
    <div>
        <!-- 导航 -->
        <div class="data-menu">
            <el-menu :default-active="activeIndex"
                class="el-menu-demo" mode="horizontal"
                @select="handleSelect">
                <el-menu-item index="a">数据A</el-menu-item>
                <el-menu-item index="b">数据B</el-menu-item>
            </el-menu>
        </div>
        <!-- 数据部分 -->
        <div class="userdata-container">
            <div class="userdata-btn">
                <el-button type="success" @click="addData">添加{{ dataType}}</el-button>
            </div>
            <el-table
                v-loading="loading"
                :data="dataList"
                style="width: 100%;margin-bottom: 20px;"
                >
                <el-table-column prop="id" align="center" width="80px"
                    label="数据序号">
                </el-table-column>
                <el-table-column :label="dataType" align="center">
                    <template slot-scope="{row}">
                        <template v-if="isedit">
                            <el-input v-model="row.data" class="edit-input" size="small" />
                        </template>
                        <span v-else>{{ row.data }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="操作" align="center">
                    <template slot-scope="scope">
                        <div v-if="isedit">
                            <el-button class="cancel-btn" size="small" icon="el-icon-refresh"
                                type="warning"
                                @click="cancelEdit"
                            >返回</el-button>
                            <el-button  size="mini" type="success" @click="handleEdit(scope.row)">确定修改</el-button>
                        </div>
                        <div v-else>
                            <el-button  size="mini" type="primary" plain @click="onEdit">编辑</el-button>
                            <el-button size="mini" type="danger" @click="onDel(scope.row)">删除</el-button>
                        </div>
                    </template>                               
                </el-table-column>
            </el-table>
        </div>
        <!-- 添加数据 -->
        <el-dialog :title="'添加'+dataType" :visible.sync="dialogFormVisible"
            @close="closeMyData">
            <el-form :model="form">
                <el-form-item :label="dataType" :label-width="formLabelWidth">
                <el-input v-model="form.data" autocomplete="off"></el-input>
                </el-form-item>
                <!-- <el-form-item label="活动区域" :label-width="formLabelWidth">
                    <el-select v-model="form.region" placeholder="请选择活动区域">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item> -->
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="cancleMyData">取 消</el-button>
                <el-button type="primary" @click="addMyData">确 定</el-button>
            </div>
        </el-dialog>

        <!-- 底部导航页 -->
        <div class="bottom-page">
            <Pagination
                :totalMsg="query.total"
                :pageSize="query.pageSize"
                :pageIndex="query.pageIndex"
                @setSize="getSize"
                @setIndex="getIndex"
            >
            </Pagination>
        </div>
        
    </div>
</template>

<script>
import Pagination from '@/components/Pagination.vue'
export default {
    components:{
        Pagination
    },
    data(){
        return{
            dialogFormVisible:false,
            formLabelWidth:'120px',
            isedit:false,
            activeIndex:'a',
            loading:false,
            dataList:[],
            dataType:'数据A',
            userArr:[],
            dataA:'',
            dataB:'',
            form:{
                data:''
            },
            query:{
                total:null,
                pageSize:5,
                pageIndex:1
            }
        }
    },
    methods:{
        getSize(size){//底部导航
            this.query.pageSize = size;
            this.getDataList();
        },
        getIndex(index){
            this.query.pageIndex = index;
            this.getDataList();
        },
        handleSelect(data){//切换数据
            let that=this;
            that.activeIndex = data;
            that.getDataList();
            switch (data){
                case 'a':
                    that.dataType = '数据A';
                    break;
                case 'b':
                    that.dataType = '数据B';
                    break;
            }
        },
        getDataList(){//获取数据
            let that = this;
            this.$api.govfinddata(`${that.activeIndex}`,{
                page: that.query.pageIndex,
                pageSize: that.query.pageSize
            }).then(res=>{
                if(res.err_code === 100){
                    let data = res.data;
                    if(that.activeIndex === 'a'){
                        let mydata = data.dataA.AllData;
                        that.dataList = mydata.list;
                        that.query.total = mydata.total;
                    }else if(that.activeIndex == 'b'){
                        let mydata = data.dataB.AllData;
                        that.dataList = mydata.list;
                        that.query.total = mydata.total;
                    }
                    that.loading = false;
                }else{
                    that.$message({
                        message:'数据获取失败',
                        type:'error'
                    })
                }
            })
        },
        addData(){//添加数据
            this.dialogFormVisible=true;
        },
        cancleMyData(){
            this.dialogFormVisible = false;
            this.form.data = '';
        },
        closeMyData(){
            this.dialogFormVisible = false;
            this.form.data = '';
        },
        addMyData(){
            let that =this;
            this.$api.govadddata(this.activeIndex,{
                data:that.form.data
            }).then(res=>{
                if(res.err_code === 100){
                    that.$message({
                        message:'添加数据成功',
                        type:'success'
                    });
                    that.getDataList();
                    that.dialogFormVisible = false;
                    that.form.data = '';
                }else{
                    that.$message({
                        message:'添加数据失败',
                        type:'error'
                    });
                }
            })
        },
        onEdit(){//数据编辑部分
            this.isedit = true;
        },
        cancelEdit(){
            this.isedit = false;
        },
        handleEdit(info){
            let that = this;
            this.$api.govupdatedata(`${that.activeIndex}`,{
                data:info.data,
                id:info.id
            }).then(res=>{
                if(res.err_code === 100){
                    that.$message({
                        message:'数据修改成功',
                        type:'success'
                    });
                    that.getDataList();
                    that.isedit = false;
                }else{
                    that.$message({
                        message:'数据修改失败',
                        type:'error'
                    });
                }
            })
        },
        onDel(info){//删除数据
            let that = this;
            this.$confirm('确定删除吗?','提示',{
                type:'warning'
            }).then(()=>{
                that.$api.govdeldata(this.activeIndex,{
                    id:info.id
                }).then(res=>{
                    if(res.err_code === 100){
                        that.$message({
                            message:'删除成功',
                            type:'success'
                        });
                        that.getDataList();
                    }else{
                        that.$message({
                            message:'删除失败',
                            type:'error'
                        });
                    }
                })
            }).catch(()=>{})   
            
        }
    },
    created(){
        this.getDataList();
    }
}
</script>

<style lang="scss" scoped>
    .userdata-btn{
        margin: 10px 0px;
    }
    .userdata-container{
        margin: 0 10px 20px;
    }
    .bottom-page{
        background: #fff;
        display: flex;
        justify-content: center;
    }
</style>